/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton ($bgcolor,$weight,$fontcolor,$display) {
    background-color: $bgcolor;
    font-weight: $weight;
    color: $fontcolor;
    text-decoration: none;
    border: none;
    padding: 1rem 3rem;
    text-align: center;
    margin-top: $separacion/2;
    display: $display;
    &:hover{
        cursor: pointer;
        background-color: darken($bgcolor,5);
    }
}